<template>
  <div class="d-flex flex-column justify-center">
    <v-card
      class="d-inline-block mr-3"
      width="100%"
      elevation="3"
      color="grey lighten-2"
    >
      <v-container>
        <v-card-actions>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn v-bind="attrs" v-on="on" class="mx-2">
                <v-icon color="#614021">{{ icons.mdiEye }}</v-icon>
              </v-btn>
            </template>
            <span>Visualizar Receita</span>
          </v-tooltip>

          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn v-bind="attrs" v-on="on" class="mx-2">
                <v-icon color="#614021">{{ icons.mdiPrinter }}</v-icon>
              </v-btn>
            </template>
            <span>Imprimir Dieta</span>
          </v-tooltip>

          <v-spacer></v-spacer>

          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="#c5d13f"
                v-bind="attrs"
                v-on="on"
              >
                <v-icon color="#614021">{{ icons.mdiDownload }}</v-icon>
              </v-btn>
            </template>
            <span>Baixar Receita</span>
          </v-tooltip>

          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="#c5d13f" v-bind="attrs" v-on="on" class="mx-4" @click="saveToFirestore">
                <v-icon color="#614021">{{ icons.mdiContentSave }}</v-icon>
              </v-btn>
            </template>
            <span>Salvar Receita</span>
          </v-tooltip>

          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="#c5d13f" v-bind="attrs" v-on="on">
                <v-icon color="#614021">{{ icons.mdiEmailSendOutline }}</v-icon>
              </v-btn>
            </template>
            <span>Enviar Receita ao Paciente</span>
          </v-tooltip>
        </v-card-actions>

        <v-divider></v-divider>

        <v-row class="d-flex justify-center">
          <v-chip color="#c5d13f" draggable>
            <v-row class="px-2">
              <h4 class="pr-2">
                TOTAL KCAL: {{ receita.KCAL.toFixed(2) }}
              </h4>
              <h4 class="pr-2">CHO: {{ receita.CHO.toFixed(2) }}</h4>
              <h4 class="pr-2">PTN: {{ receita.PTN.toFixed(2) }}</h4>
              <h4 class="pr-2">LIP: {{ receita.LIP.toFixed(2) }}</h4>
              <h4 class="pr-2">COL: {{ receita.COL.toFixed(2) }}</h4>
              <h4 class="pr-2">FIB: {{ receita.FIB.toFixed(2) }}</h4>
              <h4 class="pr-2">SOD: {{ receita.SOD.toFixed(2) }}</h4>
            </v-row>
          </v-chip>
        </v-row>
      </v-container>
    </v-card>

    <ReceitaFormEdit></ReceitaFormEdit>
  </div>
</template>

<script>
import firebase from "firebase";
import {
  mdiCalendar,
  mdiContentSave,
  mdiPrinter,
  mdiEye,
  mdiEmailSendOutline,
  mdiDownload,
  mdiMagnify,
} from "@mdi/js";

// import jspdf from "jspdf";
// import "jspdf-autotable";

// components imports
import ReceitaFormEdit from "../components/home/receitas/ReceitaFormEdit";

export default {
  components: { ReceitaFormEdit },
  data: function () {
    return {
      icons: {
        mdiCalendar,
        mdiContentSave,
        mdiPrinter,
        mdiEye,
        mdiEmailSendOutline,
        mdiDownload,
        mdiMagnify,
      },
    };
  },
  computed: {
    receita() {
      return this.$store.state.firestore.receita;
    },
    receitaId() {
      return this.$store.state.firestore.receitaId;
    },    
    alimentos() {
      return this.$store.state.firestore.alimentos;
    },
  },
  methods: {
    saveToFirestore: function() {
      firebase.firestore()
      // .collection("users").doc(sessionStorage.getItem("current-user"))
      .collection("receitas").doc(this.receitaId).set(this.receita);
    }
  },
  mounted() {
    if (this.$store.state.firestore.alimentos.length === 0) {
      this.$store.dispatch("firestore/getAlimentos");
    }
    // setTimeout(() => {
    //   this.createListWithNames();
    // }, 2000);
  },
};
</script>

