import firebase from "firebase";
import router from "../../router";


export async function userJoin(
    { commit },
    { email, password, nomeClinica, nomeSite, nome }
) {
  await firebase
    .firestore()
    .collection("users")
    .where("nomeSite", "==", nomeSite)
    .get()
    .then(function(querySnapshot) {
      if (querySnapshot.docs.length === 0) {
        firebase
          .auth()
          .createUserWithEmailAndPassword(email, password)
          .then((user) => {
            commit("setUser", user.email);
            commit("setIsAuthenticated", true);
            router.replace("/personalData");
          })
          .catch((error) => {
            commit("setUser", "");
            commit("setIsAuthenticated", false);
            if (error.code === "FIRAuthErrorCodeEmailAlreadyInUse") {
              commit("setAlertsEmailOk", true);
            }
          });

        firebase
          .firestore()
          .collection("users")
          .doc(email)
          .set({
            isPatient: false,
            joinedAt: firebase.firestore.FieldValue.serverTimestamp(),
            nomeClinica: nomeClinica,
            nomeSite: nomeSite,
            nome: nome,
            cpf: "CPF",
            crn: "CRN",
            phone: "Seu Telefone",
            phoneClients: "Telefone para seus Clientes",
            addressRoad: 'Endereo: "Avenida Paulista, 1150"',
            addressComplement:
              'Complemento: "Edifcio Don Pedro II, Sala 1302"',
            addressCep: "CEP (cdigo postal)",
            addressBairro: "Bairro",
            addressCity: "Cidade",
            addressCountry: "Pas",
            imagem:
              "https://firebasestorage.googleapis.com/v0/b/nutri-app-129e7.appspot.com/o/defaultImgs%2FdefaultProfilePic.png?alt=media&token=ecefb364-1fa1-4775-89e6-d1db88abd021",
            plan: "none",
          });
        commit("setNomeClinicaExists", false);
      } else {
        commit("setNomeClinicaExists", true);
      }
    })
    .catch(function(error) {
      console.log(error.message);
    });
}

export function userLogin({ commit }, { email, password }) {
    firebase
        .auth()
        .signInWithEmailAndPassword(email, password)
        .then((user) => {
            commit("setUser", user.user.email);
            commit("setIsAuthenticated", true);
            commit("setAlertsLoginOk", false);
            sessionStorage.setItem('isAuthenticated', true)
            sessionStorage.setItem('current-user', user.user.email)
            router.replace("/home");
        })
        .catch((error) => {
            console.log(error.message);
            sessionStorage.removeItem('isAuthenticated')
            commit("setUser", null);
            commit("setIsAuthenticated", false);
            commit("setAlertsLoginOk", true);
        });
}

export function userResetPassword({ commit }, { email }) {
    firebase
        .auth()
        .sendPasswordResetEmail(email)
        .then(function () {
            commit("setResetEmailsent", true);
            commit("setAlertsLoginOk", false);
        })
        .catch(function (error) {
            console.log(error.message);
        });
}

export function setUserPersonalData({ commit }, { userInfos, userEmail, userPictureUrl, userSignatureUrl, userLogoUrl }) {
    firebase.firestore().collection("users").doc(userEmail).set({
        cpf: userInfos.cpf,
        crn: userInfos.crn,
        phone: userInfos.phone,
        phoneClients: userInfos.phoneClients,
        addressRoad: userInfos.addressRoad,
        addressComplement: userInfos.addressComplement,
        addressCep: userInfos.addressCep,
        addressCity: userInfos.addressCity,
        addressCountry: userInfos.addressCountry,
        addressBairro: userInfos.addressBairro,
        profilePicture: userPictureUrl,
        signature: userSignatureUrl,
        logo: userLogoUrl,
        useLogo: userInfos.useLogo,
        useSignature: userInfos.userSignature
    });

    var userInfo = {
        infos: userInfos,
        mail: userEmail,
        picture: userPictureUrl,
        sign: userSignatureUrl,
        logo: userLogoUrl,
    }

    commit('setUserInfo', userInfo)
    console.log(userInfo)
    console.log(commit);
}

export function pacientePreAtendimento({ commit }, { id }) {
  commit("setPacientePreAtendimento", id);
  if (id != null) {
    router.replace("/PreAtendimento");
  }
}

export async function getAlimentos({ commit }) {
  const alimentosList = await firebase
    .firestore()
    .collection("alimentos")
    .get()
    .then((snapshot) =>
      snapshot.docs.map((doc) => {
        return {
          value: doc.id,
          data: doc.data(),
          text: doc.data().nome,
        };
      })
    );
  commit("setAlimentos", alimentosList);
}

