// dietas

export function dietaStatus({ commit }, { refeicoes }) {
  var demaisCodigos = [
    "fibra_soluvel",
    "fibra_insoluvel",
    "cinzas",
    "calcio",
    "magnesio",
    "manganes",
    "fosforo",
    "ferro",
    "sodio",
    "potassio",
    "cobre",
    "zinco",
    "retinol",
    "re",
    "era",
    "iodo",
    "selenio",
    "acido_folico",
    "acido_pantotenico",
    "tiamina",
    "riboflavina",
    "piridoxina",
    "niacina",
    "vitamina_c",
    "vitamina_d",
    "vitamina_e",
    "vitamina_b12",
    "saturados",
    "monoinsaturados",
    "poliinsaturados",
    "gordura_trans",
    "triptofano",
    "treonina",
    "isoleucina",
    "leucina",
    "lisina",
    "metionina",
    "cistina",
    "fenilalanina",
    "tirosina",
    "valina",
    "arginina",
    "histidina",
    "alanina",
    "acido_aspartico",
    "acido_glutamico",
    "glicina",
    "prolina",
    "serina",
    "umidade",
  ];
  var status = {
    KCAL: 0,
    CHO: 0,
    PTN: 0,
    LIP: 0,
    COL: 0,
    SOD: 0,
    FIB: 0,
    fibra_soluvel: 0,
    fibra_insoluvel: 0,
    cinzas: 0,
    calcio: 0,
    magnesio: 0,
    manganes: 0,
    fosforo: 0,
    ferro: 0,
    potassio: 0,
    cobre: 0,
    zinco: 0,
    retinol: 0,
    re: 0,
    era: 0,
    iodo: 0,
    selenio: 0,
    acido_folico: 0,
    acido_pantotenico: 0,
    tiamina: 0,
    riboflavina: 0,
    piridoxina: 0,
    niacina: 0,
    vitamina_c: 0,
    vitamina_d: 0,
    vitamina_e: 0,
    vitamina_b12: 0,
    saturados: 0,
    monoinsaturados: 0,
    poliinsaturados: 0,
    gordura_trans: 0,
    triptofano: 0,
    treonina: 0,
    isoleucina: 0,
    leucina: 0,
    lisina: 0,
    metionina: 0,
    cistina: 0,
    fenilalanina: 0,
    tirosina: 0,
    valina: 0,
    arginina: 0,
    histidina: 0,
    alanina: 0,
    acido_aspartico: 0,
    acido_glutamico: 0,
    glicina: 0,
    prolina: 0,
    serina: 0,
    umidade: 0,
  };
  refeicoes.forEach((item) => {
    status.KCAL += item.KCAL;
    status.CHO += item.CHO;
    status.PTN += item.PTN;
    status.LIP += item.LIP;
    status.FIB += item.FIB;
    status.COL += item.COL;
    status.SOD += item.SOD;
    for (var n = 0; n < demaisCodigos.length; n++) {
      status[demaisCodigos[n]] += item[demaisCodigos[n]];
    }
  });
  commit("setDietaStatus", status);
  commit("setDietaRefeicoes", refeicoes);
}

