<template>
  <v-app-bar>
    <v-img
      src="https://www.linkpicture.com/q/logogogo.png"
      contain
      max-height="60"
      max-width="180"
    ></v-img>
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-chip class="mx-2 blue white--text" v-bind="attrs" v-on="on"
          >PREMIUM</v-chip
        >
      </template>
      <span>Ver todos os planos deste Software!</span>
    </v-tooltip>
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-chip
          class="mx-2 white--text"
          color="#c5d13f"
          v-bind="attrs"
          v-on="on"
          href="https://www.clubedanutricionista.com.br/franquia"
          >FRANQUIA</v-chip
        >
      </template>
      <span>Seja Franqueado!</span>
    </v-tooltip>
    <v-spacer></v-spacer>

    <v-menu offset-y>
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          class="mx-1"
          value="patients"
          @mouseover="pacientesMouse = true"
          @mouseleave="pacientesMouse = false"
          v-bind="attrs"
          v-on="on"
          color="#c5d13f"
        >
          <v-tooltip bottom v-model="pacientesMouse">
            <template v-slot:activator="{ on, attrs }">
              <v-icon color="#614021" left v-bind="attrs" v-on="on">{{
                icons.mdiAccountGroup
              }}</v-icon>
            </template>
            <span>Ver Pacientes / Iniciar atendimento</span>
          </v-tooltip>
          Pacientes
        </v-btn>
      </template>
      <v-list>
        <v-list-item to="/PacienteNovo">
          <v-list-item-icon
            ><v-icon>{{ icons.mdiAccountPlus }}</v-icon></v-list-item-icon
          >
          <v-list-item-title
            >Cadastrar novo + Iniciar atendimento</v-list-item-title
          >
        </v-list-item>
        <v-list-item to="MeusPacientes">
          <v-list-item-icon
            ><v-icon>{{ icons.mdiMagnify }}</v-icon></v-list-item-icon
          >
          <v-list-item-title>Meus pacientes</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>

    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          class="mx-1"
          value="agenda"
          v-bind="attrs"
          v-on="on"
          color="#c5d13f"
        >
          <v-icon color="#614021" left>{{ icons.mdiCalendar }}</v-icon
          >Agenda
        </v-btn>
      </template>
      <span>Agenda de Pacientes</span>
    </v-tooltip>
    <v-spacer></v-spacer>

    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          class="mx-1"
          value="patients"
          v-bind="attrs"
          v-on="on"
          color="#c5d13f"
        >
          <v-icon color="#614021">{{ icons.mdiAccountHeartOutline }}</v-icon>
        </v-btn>
      </template>
      <span>Acessar rede social do Clube</span>
    </v-tooltip>

    <v-btn
      class="mx-1 white--text"
      color="#614021"
      value="logout"
      to="/"
      @click="logout"
    >
      <span>Logout</span>
    </v-btn>
  </v-app-bar>
</template>

<script>
import {
  mdiCalendar,
  mdiMagnify,
  mdiAccountPlus,
  mdiAccountGroup,
  mdiAccountHeartOutline,
  mdiCardAccountDetailsOutline,
} from "@mdi/js";

import firebase from "firebase";
import router from "../../router";

export default {
  data: function () {
    return {
      pacientesMouse: false,
      icons: {
        mdiCalendar,
        mdiAccountPlus,
        mdiMagnify,
        mdiAccountGroup,
        mdiAccountHeartOutline,
        mdiCardAccountDetailsOutline,
      },
    };
  },
  methods: {
    logout: function () {
      firebase
        .auth()
        .signOut()
        .then(function () {
          router.replace("/");
        })
        .catch(function (error) {
          console.log(error);
        });
      sessionStorage.setItem('current-user', null);  
      sessionStorage.removeItem('isAuthenticated');  
    },
  },
  // mounted() {
  //   if (this.$store.state.firestore.pacientes.length === 0) {
  //     this.$store.dispatch("firestore/getPacientes");
  //   }
  // }
};
</script>

<style scoped>
</style>


