<template>
  <div>
    <v-row class="d-flex justify-center my-5 text-h6">Editar receita salva:</v-row>
    <v-row class="d-flex justify-center align-center">
      <v-autocomplete
        label="Receita"
        :items="receitas"
        v-model="receita.id"
        style="width: 100%; max-width: 250px"
      ></v-autocomplete>
      <v-btn @click="editarReceita(receita.id)" class="mx-4" color="grey lighten-2"> 
        <v-icon left>
          {{ icons.mdiPlaylistEdit }}
        </v-icon>
        Editar Receita </v-btn>
    </v-row>
    <v-divider></v-divider>
    <v-row class="d-flex justify-center my-5 text-h6">Criar receita nova:</v-row>
    <v-row class="d-flex justify-center">
      <v-btn color="#c5d13f" to="/CriarReceitaNova" large class="black--text my-2">
      <v-icon color="#614021" left>{{ icons.mdiPlaylistPlus }}</v-icon>
        Criar Receita
      </v-btn>
    </v-row>
  </div>
</template>

<script>
// import firebase from "firebase";
import router from "../router";
import {
  mdiPlaylistPlus,
  mdiPlaylistEdit
} from "@mdi/js";

export default {
  data: function () {
    return {
      icons: {
        mdiPlaylistPlus, mdiPlaylistEdit
      },
      receitasList: [],
      receita: {
        id: "",
      },
    };
  },
  computed: {
    receitas() {
      return this.$store.state.firestore.receitas;
    },
  },
  methods: {
    editarReceita(id) {
      var receita = this.receitas.find((item) => item.value == id);
      if (receita !== undefined) {
        this.$store.dispatch("firestore/receitaId", {
            receitaId: id,
        });
        this.$store.dispatch("firestore/receitaUpdate", {
            receita: receita.data,
        });
        router.replace("/EditarReceita");
      }
    },
  },
  mounted() {
    if (this.$store.state.firestore.receitas.length === 0) {
      this.$store.dispatch("firestore/getReceitas");
    }
    // setTimeout(() => {
    //   this.createListWithNames();
    // }, 1000);
  },
};
</script>

