                LIP: <strong>{{ receita.LIP.toFixed(2) }}</strong>
              </p>
              <p class="px-2 mt-4">
                COL: <strong>{{ receita.COL.toFixed(2) }}</strong>
              </p>
              <p class="px-2 mt-4">
                SOD: <strong>{{ receita.SOD.toFixed(2) }}</strong>
              </p>
              <p class="px-2 mt-4">
                FIB: <strong>{{ receita.FIB.toFixed(2) }}</strong>
              </p>
              <p class="px-2 mt-4">
                PTN: <strong>{{ receita.PTN.toFixed(2) }}</strong>
              </p>
            </v-row>
          </v-chip>
          <v-dialog v-model="dialog" width="90%">
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                fab
                color="#c5d13f"
                class="mx-2 mt-4"
                x-small
                v-bind="attrs"
                v-on="on"
              >
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }"
                    ><v-icon color="#614021" v-bind="attrs" v-on="on">{{
                      icons.mdiDotsHorizontal
                    }}</v-icon>
                  </template>
                  <span>Mais informaes nutricionais</span>
                </v-tooltip>
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="h6"
                  >Informaes nutricionais detalhadas da dieta</span
                >
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-simple-table>
                    <template v-slot:default>
                      <tbody>
                        <tr
                          v-for="nutrient in allNutrients"
                          :key="nutrient.codigo"
                        >
                          <td>{{ nutrient.nome }}:</td>
                          <td>
                            <strong>{{
                              receita[nutrient.codigo].toFixed(2)
                            }}</strong>
                          </td>
                        </tr>
                      </tbody>
                    </template>
                  </v-simple-table>
                </v-container>
              </v-card-text>
              <v-card-actions class="d-flex justify-center">
                <v-btn color="#c5d13f" text @click="dialog = false">
                  Fechar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-row>
      </v-container>
    </v-card>

    <ReceitaForm></ReceitaForm>
